<template>
    <div class="contact">
        <text-devider text="联系我们"/>
        <div class="description">
            <p>灵臻传播集团位于深圳，旗下包括深圳市灵臻广告有限公司、深圳市口岸商旅传媒有限公司、香港灵锋广告有限公司、深圳市灵智品牌营销顾</p>
            <p>问有限公司、深圳市灵臻易美公关策划有限公司、深圳灵鱼影视有限公司、东兴市口岸商旅传媒有限公司共七家子公司。</p>
        </div>
        <div class="contact-info">
            <div class="message">
                <h3 class="title">
                    感谢您垂询灵臻传播集团，烦请通过以下任何一种方式与我们联系
                </h3>
                <div class="contact-way">
                    <div class="title">
                        地址
                    </div>
                    <div class="content">
                        深圳市福田区商报路7号天健创业大厦16楼
                    </div>
                </div>
                <div class="contact-way">
                    <div class="title">
                        联系电话
                    </div>
                    <div class="content">
                        <div>中国·大陆：0755-8377 7255</div>
                        <div>中国·香港：0852-2369 1861</div>
                    </div>
                </div>
                <div class="contact-way">
                    <div class="title">
                        邮箱
                    </div>
                    <div class="content">
                        lz@lingzhen.com.cn
                    </div>
                </div>
            </div>
            <div id="maps"></div>
            <div class="clearFix"></div>
        </div>
    </div>
</template>
<script>
import TextDevider from '../components/text-devider.vue'
import NewsCard from '../components/news-card.vue'
import BMap from '../baidu-map.js'

export default {
    name: 'Contact',
    components: {
        TextDevider,
        NewsCard
    },
    methods: {
    },
    mounted: function() {
        BMap.init().then((BMap) => {
            const map = new BMap.Map('maps');
            const point = new BMap.Point(114.049333, 22.555316);
            map.centerAndZoom(point, 15);  // 展示地图
            const marker = new BMap.Marker(point);
            map.addOverlay(marker);  // 添加标注
            const sContent = "<h4 style='margin:0 0 5px 0;padding:0.2em 0'>深圳市灵臻传媒有限公司</h4>" + 
            "<p style='margin:0;line-height:1.5;font-size:13px;'>深圳市福田区商报路7号天健创业大厦16楼</p>" + 
            "</div>";
            const infoWindow = new BMap.InfoWindow(sContent);  // 创建信息窗口对象
            marker.addEventListener("click", function(){          
                this.openInfoWindow(infoWindow);
            });
            const top_left_control = new BMap.ScaleControl({anchor: BMAP_ANCHOR_TOP_LEFT});  // 左上角，添加比例尺
            const top_left_navigation = new BMap.NavigationControl();   // 左上角，添加默认缩放平移控件
            const overView = new BMap.OverviewMapControl();   // 添加默认缩略地图控件
            const overViewOpen = new BMap.OverviewMapControl({isOpen:true, anchor: BMAP_ANCHOR_BOTTOM_RIGHT});  // 右下角，打开
            map.addControl(top_left_control);
            map.addControl(top_left_navigation);
            map.addControl(overView);
            map.addControl(overViewOpen);
        })
    }
}
</script>
<style>
