<template>
    <div class="document">
        <text-devider text="合作客户"/>
        <div class="partners">
            <el-row>
                <el-col :span="3" v-for="(logo, i) in logos" :key="i">
                    <div class="partners-item">
                        <!-- <img v-bind:src="logo.img_url" alt=""> -->
                        <img src="../assets/logos/logo11.png" alt="">
                    </div>
                </el-col>
            </el-row>
        </div>
        <text-devider text="案例"/>
        <div class="doc-album">
            <el-row :gutter="10">
                <el-col :span="12">
                    <div class="bigger" v-if="documentList[0]">
                        <router-link :to="{ path: '/detail'}">
                            <img :src="documentList[0].img_url" alt="">
                            <div class="info">
                                <div class="title">{{documentList[0].title}}</div>
                                <div class="desc">{{documentList[0].desc}}</div>
                            </div>
                        </router-link>
                    </div>
                </el-col>
                <el-col :span="12">
                    <el-row :gutter="4">
                        <el-col :span="12" v-for="(smaller_1, index) in documentList" :key="index" v-if="index > 0 && index < 5">
                            <div class="smaller">
                                <router-link :to="{ path: '/detail'}">
                                    <img :src="smaller_1.img_url" alt="">
                                    <div class="info">
                                        <div class="title">{{smaller_1.title}}</div>
                                        <div class="desc">{{smaller_1.desc}}</div>
                                    </div>
                                </router-link>
                            </div>
                        </el-col>
                    </el-row>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :span="12">
                    <el-row :gutter="4">
                        <el-col :span="12" v-for="(smaller_2, index) in documentList" :key="index" v-if="index > 4 && index < 9">
                            <div class="smaller">
                                <router-link :to="{ path: '/detail'}">
                                    <img :src="smaller_2.img_url" alt="">
                                    <div class="info">
                                        <div class="title">{{smaller_2.title}}</div>
                                        <div class="desc">{{smaller_2.desc}}</div>
                                    </div>
                                </router-link>
                            </div>
                        </el-col>
                    </el-row>
                </el-col>
                <el-col :span="12">
                    <div class="bigger" v-if="documentList[9]">
                        <router-link :to="{ path: '/detail'}">
                            <img :src="documentList[9].img_url" alt="">
                            <div class="info">
                                <div class="title">{{documentList[9].title}}</div>
                                <div class="desc">{{documentList[9].desc}}</div>
                            </div>
                        </router-link>
                    </div>
                </el-col>
            </el-row>
        </div>
        <div class="more" v-loading="loading">
            <button @click="loadmore()">LOAD&nbsp;&nbsp;MORE</button>
        </div>
    </div>
</template>
<script>
import TextDevider from '../components/text-devider.vue'
import NewsCard from '../components/news-card.vue'
export default {
    name: 'Document',
    components: {
        TextDevider,
        NewsCard
    },
    data() {
        return {
            loading: false,
            logos: [],
            documentList: []
        }
    },
    mounted: function() {
        for (let i = 0; i < 40; i++) {
            const index = i + 1;
            this.logos.push({img_url: './assets/logos/logo'+index+'.png', index: index})
        }
        this.getDocument();
    },
    methods: {
        loadmore: function() {
            this.loading = true;
            setTimeout(() => {
                this.loading = false;
            }, 3000);
        },
        getDocument: function() {
            this.$http
                .get("http://60.205.191.12:8081/api/articles", { params: { type: 4, count: 10 } })
                .then(response => {
                    const result = JSON.parse(response.bodyText);
                    this.documentList = result.data[0].list;
                });
        },
    }
}
</script>
<style>
.partners .partners-item {
    width: 100px;
    height: 100px;
    border-radius: 50px;
    overflow: hidden;
    display: inline-block;
    margin-bottom: 20px;
    background: #fff;
}
.partners .partners-item img {
    width: 100%;
    height: 100%;
}
.partners .el-col {
    text-align: center;
}
.doc-album .bigger, .doc-album .smaller {
    padding: 2px 0;
    box-sizing: border-box;
    position: relative;
}
.doc-album .bigger {
    width: 635px;
    height: 300px;
    overflow: hidden;
}
.doc-album .smaller {
    width: 315px;
    height: 150px;
    overflow: hidden;
}
.doc-album img{
    width: 100%;
    height: 100%;
}
.doc-album .bigger:hover .info, .doc-album .smaller:hover .info {
    opacity: 1;
}
.doc-album .info {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    margin: 0;
    color: #fff;
    text-align: center;
    padding-top: 80px;
    opacity: 0;
    transition: all 0.5s ease-in;
}
.doc-album .bigger .info {
    padding-top: 80px;
}
.doc-album .smaller .info {
    padding-top: 35px;
}
.doc-album .bigger .info .title {
    font-size: 26px;
    padding: 20px;
}
.doc-album .bigger .info .desc {
    font-size: 16px;
}
.doc-album .smaller .info .title {
    font-size: 16px;
    padding: 10px;
}
.doc-album .smaller .info .desc {
    font-size: 12px;
}
.more {
    width: 100%;
    text-align: center;
    padding: 60px 0;
}
.more button{
    width: 330px;
    height: 54px;
    border: 1px solid #333;
    background: #fff;
    font-size: 20px;
    cursor: pointer;
}
</style>