<template>
    <div class="article">
        <div class="article-content">
            <span>{{article.updated_at | moment('YYYY年M月DD日')}}</span>
            <h1 class="title">{{article.title}}</h1>
            <div v-html="article.detail"></div>
        </div>
        <ul class="newest">
            <li class="header">最热门新闻</li>
            <li class="news-item">
                <h2 class="news-title">{{news.title}}</h2>
                <span class="timer">{{news.updated_at | moment('YYYY年M月DD日')}}</span>
            </li>
        </ul>
    </div>
</template>
<script>
export default {
    name: 'article',
    data() {
        return {
            id: String,
            article: Object,
            news: Object
        }
    },
    mounted: function() {
        window.scrollTo(0, 0);
        this.id = this.$route.params.id;
        this.getArticle(this.id);
        this.getNews();
    },
    methods: {
        getArticle: function(id) {
        this.$http
            .get("http://60.205.191.12:8081/api/articles/" + id, {params: { type: 4, count: 6 }})
            .then(response => {
                const result = JSON.parse(response.bodyText);
                this.article = result.data.article;
            });
        },
        getNews: function() {
        this.$http
            .get("http://60.205.191.12:8081/api/articles", { params: { type: 3, count: 1 } })
            .then(response => {
                const result = JSON.parse(response.bodyText);
                this.news = result.data[0].list[0];
            });
        }
    }
}
</script>
<style>
.article {
    position: relative;
}
ul, ul li{
    list-style-type: none;
    margin: 0;
    padding: 0;
}
.article .newest {
    width: 200px;
    position: absolute;
    right: 0;
    top: 0;
    border-left: 4px solid #333;
    padding-left: 30px;
    color: #333;
}
.news-item .news-title, .article .article-content .title {
    margin: 16px 0;
}
.article .newest .header {
    font-weight: bold;
    font-size: 24px;
}
.news-item .news-title {
    font-size: 18px;
    margin-bottom: 8px;
}
.news-item .timer {
    font-size: 13px;
}
.article .article-content {
    margin-right: 300px;
}
</style>
