<template>
    <div class="home">
        <el-carousel indicator-position="outside" class="banner">
            <el-carousel-item class="banner-item">
                <img src="../assets/banner/1.jpg" alt="">
            </el-carousel-item>
            <el-carousel-item class="banner-item">
                <img src="../assets/banner/2.jpg" alt="">
            </el-carousel-item>
            <el-carousel-item class="banner-item">
                <img src="../assets/banner/3.jpg" alt="">
            </el-carousel-item>
            <el-carousel-item class="banner-item">
                <img src="../assets/banner/4.jpg" alt="">
            </el-carousel-item>
        </el-carousel>
        <text-devider text="案例"/>
        <div class="documents">
            <el-row :gutter="10">
                <el-col :span="8" v-for="doc in documentList" :key="doc.id">
                    <div class="doc-item">
                      <router-link :to="{ path: '/detail'}">
                        <img v-bind:src="doc.img_url" alt="">
                        <div class="info">
                            <div class="title">{{documentList[0].title}}</div>
                            <div class="desc">{{documentList[0].desc}}</div>
                        </div>
                      </router-link>
                    </div>
                </el-col>
            </el-row>
        </div>
        <text-devider text="服务内容"/>
        <div class="services">
            <div class="services-item">
                <img src="../assets/service01.png" alt="">
                <div class="desp">
                    <span class="icon"><img src="../assets/icon01.png" alt=""></span>
                    <span class="text">品牌战略</span>
                    <span class="text">咨询管理</span>
                </div>
            </div>
            <div class="services-item">
                <img src="../assets/service02.png" alt="">
                <div class="desp">
                    <span class="icon"><img src="../assets/icon02.png" alt=""></span>
                    <span class="text">全媒体</span>
                    <span class="text">整合传播</span>
                </div>
            </div>
            <div class="services-item">
                <img src="../assets/service03.png" alt="">
                <div class="desp">
                    <span class="icon"><img src="../assets/icon03.png" alt=""></span>
                    <span class="text">媒体</span>
                    <span class="text">开发与运营</span>
                </div>
            </div>
            <div class="services-item">
                <img src="../assets/service04.png" alt="">
                <div class="desp">
                    <span class="icon"><img src="../assets/icon04.png" alt=""></span>
                    <span class="text">公关</span>
                    <span class="text">互动营销</span>
                </div>
            </div>
            <div class="services-item">
                <img src="../assets/service05.png" alt="">
                <div class="desp">
                    <span class="icon"><img src="../assets/icon05.png" alt=""></span>
                    <span class="text">影视创意</span>
                    <span class="text">拍摄制作</span>
                </div>
            </div>
        </div>
        <text-devider text="最新动态"/>
        <div v-for="ne in news" :key="ne.id" class="news-item">
            <news-card bg-color="#eaeaea" :source="ne"/>
        </div>
    </div>
</template>
<script>
import TextDevider from "../components/text-devider.vue";
import NewsCard from "../components/news-card.vue";

export default {
  name: "Home",
  components: {
    TextDevider,
    NewsCard
  },
  data() {
    return {
      documentList: [],
      news: []
    };
  },
  mounted: function() {
    this.getDocument();
    this.getNews();
  },
  methods: {
    getDocument: function() {
      this.$http
        .get("http://60.205.191.12:8081/api/articles", {
          params: { type: 4, count: 6 }
        })
        .then(response => {
          const result = JSON.parse(response.bodyText);
          this.documentList = result.data[0].list;
        });
    },
    getNews: function() {
      this.$http
        .get("http://60.205.191.12:8081/api/articles", { params: { type: 3 } })
        .then(response => {
          const result = JSON.parse(response.bodyText);
          this.news = result.data[0].list;
        });
    }
  }
};
</script>
<style>
.el-carousel__button {
  width: 10px;
  height: 10px;
  border-radius: 5px;
}
.el-carousel__indicator.is-active .el-carousel__button {
  background-color: #e00012;
}
.el-carousel__indicators--outside button {
  background-color: #c4c5c5;
}
.el-row {
  margin-bottom: 10px;
}
.el-row:last-child {
  margin-bottom: 0;
}
.el-carousel__container {
  height: 530px;
}

.banner-item img {
  width: 100%;
}
.documents .el-col {
  margin-bottom: 10px;
}
.documents .doc-item {
  position: relative;
  height: 250px;
  overflow: hidden;
}
.documents .doc-item img {
  width: 100%;
}
.documents .doc-item .info {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    margin: 0;
    color: #fff;
    text-align: center;
    padding-top: 70px;
    opacity: 0;
    transition: all 0.5s ease-in;
}
.documents .doc-item:hover .info {
    opacity: 1;
}
.doc-item .info .title {
    font-size: 20px;
    padding: 10px;
}
.doc-item .info .desc {
    font-size: 16px;
}
.services {
  padding: 30px 0;
}
.services .services-item {
  width: 20%;
  box-sizing: border-box;
  display: inline-block;
  padding: 0 4px;
  height: 258px;
  overflow: hidden;
  position: relative;
}
.services .services-item img {
  width: 100%;
}
.services .services-item .desp {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  color: #fff;
  margin: auto;
  text-align: center;
  padding-top: 60px;
}
.desp .text {
  display: block;
  text-align: center;
  width: 100%;
  font-weight: bold;
  font-size: 16px;
  letter-spacing: 1px;
}
.desp .icon {
  display: inline-block;
  width: 84px;
  height: 84px;
  margin-bottom: 14px;
}
.home .news-item {
  margin-bottom: 70px;
}
.home .news-item:last-child {
  margin-bottom: 0;
}
</style>