<template>
    <div class="join">
        <text-devider text="加入我们"/>
        <div class="pic">
            <img src="../assets/join.jpg" alt="加入我们">
        </div>
        <div class="description">
            <p>我们的团队拥有丰富多样的人才。涵盖了品牌咨询顾问、广告营销、媒体广告、影视创意、公关营销等领域，我们倡导充满活力的工作氛围</p>
            <p>同时制定完善的人才激励制度，为员工提供良好的职业发展空间。诚邀有识之士的加入。</p>
        </div>
        <text-devider text="招贤纳士"/>
        <div class="company-list">
            <div class="company-list-item" v-for="company in companys" :key="company.id">
                <div class="title">
                    {{company.name}}
                </div>
                <div class="jobs">
                    <el-collapse v-model="activejob" accordion v-if="company.jobs.length > 0">
                        <el-collapse-item :name="job.id" v-for="job in company.jobs" :key="job.id">
                            <template slot="title">
                            {{job.title}}
                            </template>
                            <div v-html="job.detail"></div>
                        </el-collapse-item>
                    </el-collapse>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import TextDevider from "../components/text-devider.vue";
import NewsCard from "../components/news-card.vue";
export default {
    name: "Join",
    components: {
        TextDevider,
        NewsCard
    },
    data() {
        return {
            companys: [],
            activejob: 0
        };
    },
    mounted: function() {
        this.getJobs();
    },
    methods: {
        getJobs: function() {
            this.$http
                .get("http://60.205.191.12:8081/api/jobs", { params: { count: 100 } })
                .then(response => {
                    const result = JSON.parse(response.bodyText);
                    this.companys = result.data[0].list;
                });
        }
    }
};
</script>
<style>
.el-collapse {
  border: none;
}
.el-collapse .el-icon-arrow-right {
  font-size: 14px;
}
.el-collapse .el-icon-arrow-right:before {
  content: "点击查看详情";
  color: #333333;
}
.el-collapse-item__arrow.is-active {
  transform: none;
}
.el-collapse-item__header {
  width: 50%;
  font-size: 18px;
  font-weight: bold;
  border-color: #BEBFBE;
  padding: 6px 0;
}
.el-collapse-item__content {
  background: #e3e3e3;
  padding: 30px;
}
.join .company-list-item {
    margin-bottom: 60px;
}
.join .company-list-item .title {
  display: inline-block;
  padding: 10px 20px;
  margin: 20px 0;
  background: #c4c5c5;
  font-size: 22px;
  font-weight: bold;
}
.join .pic img {
    width: 100%;
}
</style>