<template>
    <div class="newscard" v-bind:style="{'background-color': bgColor}">
        <el-row>
            <el-col :span="6">
                <div class="pic">
                    <img :src="source.img_url">
                </div>
            </el-col>
            <el-col :span="18">
                <div class="news-info">
                    <div class="title" :style="{'font-weight': source.sub_title ? 'normal' : 'bold', 'font-size': source.sub_title ? '16px' : '18px'}">
                        <router-link :to="{ name: 'article', params: { id: source.id }}" v-if="!noLink">{{source.title}}</router-link>
                        <span v-if="noLink">{{source.title}}</span>
                    </div>
                    <div class="sub-title" v-if="source.sub_title">{{source.sub_title}}</div>
                    <div class="desc">{{source.desc}}</div>
                    <div class="timer">
                        <span v-if="source.updated_at || source.from">{{source.updated_at | moment('YYYY-MM-DD')}}{{'｜来源：' + source.from}}</span>
                    </div>
                </div>
            </el-col>
        </el-row>
    </div>
</template>
<script>
export default {
  name: "NewsCard",
  props: {
      bgColor: {
          type: String,
          default: 'inherit'
      },
      source: {
          type: Object,
          default() { 
                return {
                    title: null,
                    desc: null,
                    detail: null,
                    img_url: null,
                    update_time: null,
                    from: null
                };
            }
      },
      noLink: {
          type: Boolean,
          default: false
      }
  }
};
</script>
<style>
.newscard {
    padding: 30px;
}
.newscard .pic {
    padding-right: 30px;
    height: 180px;
    overflow: hidden;
}
.newscard .pic img {
    width: 100%;
}
.newscard .news-info {
    height: 180px;
    position: relative;
}
.newscard .news-info .title {
    font-size: 16px;
    font-weight: bold;
    width: 518px;
    overflow: hidden;
    text-overflow: ellipsis;
    word-wrap: none;
    border-bottom: 1px solid #333;
    padding: 6px 0 14px;
}
.newscard .news-info .sub-title {
    font-size: 18px;
    font-weight: bold;
    padding-top: 14px;
}
.newscard .news-info .desc {
    padding: 14px 0;
}
.newscard .news-info .title a:hover {
  text-decoration: underline;
}
.newscard .news-info .timer {
    position: absolute;
    bottom: 4px;
    left: 0;
    font-size: 14px;
}
</style>
